<template>
 <div class="questionnaireOperation">
     <answer-pc-handle :id="id"></answer-pc-handle>
 </div>
</template>

<script>
import AnswerPcHandle from '../components/AnswerPcHandle'
export default {
 name: 'questionnaireOperation',
 components: {AnswerPcHandle},
 data() {
 return {
   id:this.$route.query.id || '',
 }
 },
 created(){},
 mounted(){},
 methods:{}
}
</script>

<style lang='scss' scoped>
</style>



