<template>
 <div class="t">
    <div class="t_container">
         <div class="t_content">
             <div class="t_content_head">
                <div class="t-title mtop"><p v-html="formData.formTitle"></p></div>
                <div class="t-intro mtop">{{formData.remarks}}</div>
                <div class="t-hr mtop"></div>
             </div>
             <div class="t_content_body">
                  <div v-for="(vo, index) in list" :key="index" class="t-ques maxtop">
                     <div v-if="vo.type === 'baseRemark'"  class="t-ques_title" :class="{' t-required': vo.required}"> {{vo.title}} </div>                                  
                     <div v-if="vo.type !== 'splitLine' && vo.type !== 'baseRemark'" class="t-ques_title" :class="{' t-required': vo.required}">
                         <span class="qnum">{{vo.sort}}.</span><span>{{vo.title}}</span>
                     </div>
                     <div v-if="vo.type === 'splitLine'" class="line"></div>
                     <div class="t-ques_matrix">
                         <div v-if="vo.type ==='gradeRadio' || vo.type ==='sex'" class="t-checkbox">
                             <el-radio-group v-model="vo.questionValue" class="radio-box">
                                <el-radio v-for="(single, n) in vo.items" :key="n" :label="single.text">
                                    {{single.text}}  
                                    <el-input-number class="t-input" v-model="single.personNumber" controls-position="right"  :min="0"></el-input-number>
                                </el-radio>
                            </el-radio-group>
                         </div>
                         <div v-if="vo.type ==='gradeCheckbox'" class="t-checkbox">
                            <el-checkbox-group v-model="vo.questionValue" class="checkbox-box">
                                <el-checkbox v-for="(single, n) in vo.items" :key="n" :label="single.text">
                                      <span class="el-checkbox__label">{{single.text}}</span>
                                     <el-input-number class="t-input" v-model="single.personNumber" controls-position="right"  :min="0"></el-input-number>
                                </el-checkbox>                   
                            </el-checkbox-group>
                         </div>
                          <div v-if="vo.type ==='baseText' || vo.type ==='name' || vo.type ==='phone' || vo.type ==='email' || vo.type ==='academy' || vo.type ==='specialty' || vo.type ==='industry' || vo.type ==='profession'" class="t-checkbox">
                            <el-input class="t-input" v-model="vo.questionValue"></el-input>
                         </div>
                          <div v-if="vo.type === 'baseTextArea'" class="t-checkbox">
                            <el-input class="t-input" type="textarea"  :rows="2" v-model="vo.questionValue"></el-input>
                         </div>
                         <div v-if="vo.type === 'baseGauge'" class="t-checkbox">
                             <div  class="radio-box-row">
                                <my-rate :total="vo.itemNum" :parentIndex="index" :isNumber="true" @change="myRateChange" @numberChange="myNumberChange"></my-rate>
                            </div>
                            <p class="tip" :class="{'tip_five': vo.itemNum === 5}"><span>{{vo.startDesc}}</span><span>{{vo.endDesc}}</span></p>
                         </div>
                         <div v-if="vo.type === 'uploadFile'" class="t-checkbox q_file_upload">
                              <input  type="file" name="file" ref="inputFile"  @change="uploadChange"  style="display: none;">
                             <div v-if="vo.questionValue" class="q_file_upload_img">
                                <label>{{vo.desc}}</label> 
                                <div class="df">
                                    <label @click="chooseFile(index)">重新选择&nbsp;&nbsp;</label>|
                                    <span @click="deleteFile(index)">&nbsp;&nbsp;删除</span>
                                </div>
                             </div>
                             <div v-else  @click="chooseFile(index)"  class="q_file_upload_up">                                
                                  <div class="m_upload_btn"><i class="el-icon-upload"></i><label >&nbsp;&nbsp;选择文件（30M以内）</label></div>
                             </div>                
                         </div>
                         <div v-if="vo.type === 'matrixGauge'" class="t-checkbox">
                           <table class="q_table">
                             <tbody>
                                <tr class="q_table_head">
                                    <td class="row_td"></td> 
                                    <td style="text-align:left;" class="row_content">
                                        <p><span v-for="(start, n) in vo.itemNum" :key="n" class="t_span">{{n === 0 ? '非常不满意' : n === vo.itemNum -1 ? '非常满意' : ''}}</span></p>
                                        <p><label v-for="(vo, i) in vo.itemNum" :key="i">{{i +1}}</label></p>
                                    </td>
                                </tr>
                                <tr v-for="(row, rowIndex) in vo.rowDesc" :key="rowIndex">
                                    <td style="text-align:left;" class="row_td">{{row.text}} </td>
                                    <td class="row_content"><my-rate :total="vo.itemNum" :isText="false" :isNumber="true" :parentIndex="index" :childIndex="rowIndex" @change="myRateChange"  @numberChange="myNumberChange"></my-rate></td>
                                </tr>
                                </tbody>
                            </table>
                         </div>
                     </div>
                 </div>
             </div>
             <div class="end_remarks">{{formData.endWord}}</div>
             <div class="t_footer"><el-button @click="submit" type="primary">提交</el-button></div>
         </div>
    </div>
 </div>
</template>

<script>
import MyRate from '@/components/MyRate/index'
export default {
 name: 'AnswerPcHandle', //带输入框操作
 components: {MyRate},
 props: {
    id: {
      type: String,
      default: ''
    },
    type: {
      type: String,
      default: 'inside'
    }
  },
 data() {
 return {
   formData:{
     formTitle:'',
     remarks:'',
     endWord:''
   },
   list:[],
   isSubmit:false,
   inputFileIndex:null,
   form:{
     formId:'',
     createUser:'',
     data:'',
     ip:''
   },
   api:{
      uploadUrl:`${this.API_URL}/system/file/editorUpload?x-token=${localStorage.getItem("x_token")}`,
      queryQuestionAll:'/form/rest/FormQuestionBaseBusiness/queryQuestionAll/v1',
      save:'/form/rest/FormAnserBusiness/save/v1'
   }
 }
 },
 created(){
    this.initData()
 },
 mounted(){},
 methods:{
    initData(){
      const postData={
         formId:this.id
      }
      this.$post(this.api.queryQuestionAll , postData)
         .then(res => {
            if(res.code ===200){
                let num=0
                this.formData=res.data.form
                const arr=res.data.questions || []
                arr.forEach((_vo, _index) =>{
                     if(_vo.type === 'gradeRadio' || _vo.type === 'gradeCheckbox' || _vo.type === 'sex'){
                         _vo.items.forEach(v =>{
                              v.personNumber=0
                         })       
                     }
                    if(_vo.type === 'baseGauge'){
                           _vo.questionPerson=[]  
                     }
                     if( _vo.type === 'matrixGauge'){                        
                          _vo.rowDesc.forEach(v =>{
                               v.questionPerson=[]
                         })    
                     }
                    if(_vo.type === 'gradeCheckbox' || _vo.type === 'matrixGauge'){
                        _vo.questionValue=[]
                    }else{
                        _vo.questionValue=''
                    } 
                    if(_vo.type === 'splitLine' || _vo.type === 'baseRemark'){
                        _vo.sort=null
                    }else{ 
                        num+=1
                        _vo.sort=num
                    }
                })
                this.list=arr
               
           }
         })
    },
    deleteFile(index){
      this.list[index].questionValue=''
    },
    chooseFile(index){
        this.inputFileIndex=index
        this.$refs.inputFile[0].click()    
    },
    uploadChange(){
      let files=this.$refs.inputFile[0].files[0]
      const isLt2M = files.size > 31457280;
      if(isLt2M){
          this.$notify.error({
                title: 'error',
                duration:1500,
                message: '请选择30M以内文件'
            })
          return
      }
      let formData = new window.FormData()
      formData.append('file', files)
      this.$post(this.api.uploadUrl,formData).then(res =>{
         if(res.success ===0){
              this.list[this.inputFileIndex].desc=files.name
              this.list[this.inputFileIndex].questionValue=res.id
          }else{
              this.$notify.error({
                title: 'error',
                duration:1500,
                message: '上传失败，请重新上传'
            })
          }
      })
    },
    myRateChange(value, parentIndex, childIndex){
      if(childIndex === null){ 
         this.list[parentIndex].questionValue=value   
      }else{ 
         this.list[parentIndex].rowDesc[childIndex].questionValue=value
      } 
    },
    myNumberChange(parentIndex, childIndex, numberArr){
       if(childIndex === null){ 
         this.list[parentIndex].questionPerson=numberArr
      }else{ 
         this.list[parentIndex].rowDesc[childIndex].questionPerson=numberArr
      } 
    },
    submit(){
      if(this.isSubmit){
          this.$message({
            message: '请勿重复提交！',
            type: 'warning'
          });

          return
      }
      //必填项校验
    //   if(!this.validateRequired(this.list)){
    //       return
    //   } 
      const newList=JSON.parse(JSON.stringify(this.list))
      const submitArr=[]
      newList.forEach(item =>{
            if(item.type !== 'baseRemark' && item.type !== 'splitLine' ){    
                let qVal=null
                let person=[]            
                 if(item.type === 'gradeRadio' || item.type === 'sex'){
                    qVal=item.questionValue  
                    const personArr=[]
                    item.items.forEach(vo => { 
                         personArr.push(vo.personNumber)      
                    })
                    person=personArr
                    
                }else if(item.type === 'gradeCheckbox'){
                    qVal=item.questionValue 
                    const personArr=[]
                    item.items.forEach(vo => { 
                         personArr.push(vo.personNumber)      
                    })
                    person=personArr
                }else if(item.type === 'baseGauge'){
                    person=item.questionPerson
                }else if(item.type === 'matrixGauge'){
                    const matrixGaugeArr=[]
                    item.rowDesc.forEach(vo => { if(vo.questionValue){matrixGaugeArr.push(vo.questionValue)}})
                    person=item.rowDesc
                    qVal=matrixGaugeArr
                }else{
                    qVal=item.questionValue  
                }
                submitArr.push({
                    questionId:item.id,
                    questionValue: qVal,
                    questionPerson:person
                })
                
        }         
             
       })
       console.log(submitArr)
       this.form.formId=this.id;
       this.form.data=JSON.stringify(submitArr)
       this.form.ip=localStorage.getItem('Ip')
       this.saveFn()
    },
    saveFn(){
       this.$post(this.api.save, this.form).then(res =>{
           if(res.code ===200){
               this.isSubmit=true;
               this.$notify.success({
                    title: 'success',
                    duration:1500,
                    message: '提交成功'
                    
                })
                if(this.type === 'inside'){
                    this.$emit('onSuccess', true)
                }  
           }else{
               this.isSubmit=false;
               this.$notify.error({
                    title: 'error',
                    duration:1500,
                    message: res.msg
                })
           }
       }).catch(err =>{
           this.isSubmit=false
       }) 
    },
    //校验函数
    validateRequired(arr){
        let validate =false;
        for(let i=0; i<arr.length; i++){
            if(arr[i].type !== 'baseRemark' && arr[i].type !== 'splitLine' && arr[i].required){  
                     const msg= (scope) => `${arr[i].sort}. ${arr[i].title} ${scope}`
                    if((arr[i].type ==='gradeRadio' || arr[i].type === 'sex') && !arr[i].questionValue){
                        this.$message.error(msg('是必选项，请选择选项'));
                        validate=false;
                        break;
                    }else  if(arr[i].type ==='gradeCheckbox' && arr[i].questionValue.length ===0){
                        this.$message.error(msg('是必选项，请选择选项'));
                        validate=false;
                        break;
                    }else  if(arr[i].type ==='baseGauge' && arr[i].questionValue === ''){
                        this.$message.error(msg('是必选项，请为选项打分'));
                        validate=false;
                        break;
                    }else  if(arr[i].type ==='matrixGauge'){
                        let num=0;
                        arr[i].rowDesc.forEach(vo =>{
                             if(vo.questionValue) num ++
                        })
                        if(num!==arr[i].rowDesc.length){
                            this.$message.error(msg('是必选项，请为所有选项打分'));
                            validate=false;
                             break;
                        }else{
                            validate=true;  
                        }    
                    }else if(arr[i].type ==='phone'){
                        if(arr[i].questionValue){
                             const reg = /^[1][3,4,5,7,8][0-9]{9}$/;
                             if (reg.test(arr[i].questionValue)){
                                  validate=true; 
                             }else{  
                                this.$message.error(msg('请输入正确的手机号码'));
                                validate=false;
                                break; 
                             }
                        }else{
                            this.$message.error(msg('是必填项，请输入内容'));
                            validate=false;
                            break; 
                        }

                    }else if(arr[i].type ==='email'){
                        if(arr[i].questionValue){
                             const reg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
                             if (reg.test(arr[i].questionValue)){                
                                    validate=true; 
                             }else{
                                  this.$message.error(msg('请输入正确的邮箱地址'));
                                   validate=false;
                                   break; 
                             }
                        }else{
                            this.$message.error(msg('是必填项，请输入内容'));
                            validate=false;
                            break; 
                        }

                    }else if(!arr[i].questionValue){
                       this.$message.error(msg('是必填项，请输入内容'));
                       validate=false;
                         break;
                    }else{
                        validate=true;
                    }
                                        
            }
        }
        
        return validate
    }
 }
}
</script>

<style lang='scss'>
.t{
    height: 100%;
    background-color: #f5f5f5;
    background-position: center top;
    text-align: left;
    padding:60px 0 100px 0;
    overflow-y: auto;
    .t_container{
        width: 814px;
        min-height: 200px;
        margin: 0px auto 10px auto;
        position: relative;
        background: #fff;
        word-break: normal;
        z-index: 99;
        color:#666;
        font-size:14px;
       .mtop{
          margin-top: 20px;
       }
       .maxtop {
          width: 734px;
          margin-top: 20px;
        }
       .t_content{
            padding: 30px 40px 66px;
            .t_content_head{
                clear: both;
                overflow: hidden;
                .t-title{
                    p{
                       color: #4C4C4C;
                       font-size: 18px;
                    }
                }
                .t-intro{
                    font-size: 16px;
                }
                .t-hr{
                    width: 100%;
                    height: 3px;
                    background-color: #03B187;
                }
            }
            .t_content_body{
                .t-ques{
                    padding-left: 15px;
                    .line{
                        border-bottom: 1px dashed #ccc;
                        height: 20px;
                        margin-left: 5px;
                    }
                    .t-ques_title{
                        width: 100%;
                        font-size: 16px;
                        margin-left: -15px;
                        padding-bottom: 8px;
                        position: relative;
                        padding-left: 5px;
                        .qnum{
                            top: 0;
                            left: -42px;
                            position: absolute;
                            text-align: right;
                            width: 45px;
                        }
                        span:last-child{
                            word-break: break-all;
                        }
                    }
                    .t-required>*:last-child:after{
                        content: "*";
                        display: inline;
                        color: #ff6d56;
                        margin-left: 5px;
                    }
                    .t-ques_matrix{
                        overflow-x: auto;
                        overflow-y: hidden;
                        width: 100%;
                        .t-checkbox{
                            position: relative;
                            padding-bottom: 10px;
                            .radio-box{
                                width: 100%;
                                margin-top: 10px;
                                .el-radio{
                                  width: 100%;
                                  color: #666;
                                  margin-bottom: 10px;  
                                  margin-right: 0;   
                                  .is-checked+.el-radio__label{
                                    color: #666 !important;
                                 }                           
                                }                                
                            }
                            .radio-box-row{
                                 width: 462px;
                            }
                            .checkbox-box{
                                width: 100%;
                                margin-top: 10px;
                                .el-checkbox{
                                    width: 100%;
                                   color: #666;
                                   margin-bottom: 10px;  
                                   margin-right: 0;  
                                  .is-checked+.el-checkbox__label{
                                    color: #666 !important;
                                 }  
                                }
                            }
                            .tip{
                                width: 420px;
                                height:20px;
                                margin-top:10px;
                                span{
                                    display: inline-block;
                                    width: 50%;
                                    height: 100%;
                                    overflow: hidden;
                                    line-height: 1.5;
                                    word-wrap: break-word;
                                }
                                 span:nth-of-type(2){
                                   text-align:right;
                                }
                            }
                            .tip_five{
                                width: 200px; 
                            }
                            .t-input{
                                width:150px;
                                line-height: 35px;
                                .el-input-number__increase, .el-input-number__decrease{
                                    top:2px;
                                }
                                .el-input__inner{
                                    height: 35px;
                                    line-height: 35px;
                                }
                            }
                            .t-upload{
                                .el-upload{
                                    i{
                                      color:#03B187;
                                    }
                                    .el-upload__tip{
                                       display:inline-block;
                                    }
                                    .el-upload__tip:hover{
                                        color:#03B187;
                                    }
                                }
                            }
                            .q_table{
                                border-spacing: 0;
                                border: 1px solid #ddd;
                                border-collapse: separate;
                                border-left: 0;
                                border-top: 0;  
                                .q_table_head{
                                    td{
                                        background: #FBFBFB;
                                    }
                                }                      
                                th,td{
                                    border-top: 1px solid #ddd;
                                    line-height: 20px;
                                    padding: 8px;
                                    color: #666;
                                    font-size: 14px;
                                    border-left: 1px solid #ddd;
                                }
                                td{
                                    overflow: hidden;
                                    p{
                                        font-size:12px;
                                        color:#aaa;
                                        .t_span:last-child{
                                            float:right;
                                            margin-right: 35px;
                                        }
                                        label{
                                            padding:0 30px;
                                        }
                                    }
                                    .my-rate{
                                        margin-left:30px;
                                        .my-rate_item{
                                            margin-right:50px;
                                        }
                                    }
                                }
                                .row_td{
                                    width:270px;
                                } 
                                .row_content{
                                    width: 396px;
                                }                                                               
                            }
                            .q_tabel_ten{
                                tr{
                                    td{
                                      p{
                                        label{
                                            padding:0 12px;
                                        }
                                      }
                                      .my-rate{
                                            margin-left:12px;
                                            .my-rate_item{
                                                margin-right:16px;
                                            }
                                        }  
                                    }
                                }
                            } 
                        }
                        .q_file_upload{
                            padding: 7px 12px;
                            background: #fff;
                            border: 1px solid #D8D8D8;
                            border-radius: 2px;
                            width: 100%;
                            height: 40px;
                            box-sizing: border-box;
                            cursor: pointer;
                            text-align: center;
                            .q_file_upload_up{
                                .m_upload_btn{
                                    i{
                                    color: #03B187;
                                    }
                                    label{
                                    cursor: pointer;
                                    }    
                                }
                            }                        
                            .q_file_upload_img{
                                display:flex;
                                align-items: center;
                                justify-content: space-between;
                               img{
                                   width:24px;
                                   height:24px;
                               }
                               .df{
                                   display:none;
                                 label{
                                    font-size: 12px;
                                    color: #03B187;
                                     cursor: pointer;
                                 }
                                 span{
                                  font-size: 12px;
                                  color: red;
                                 }
                               }
                            }
                        }
                        .q_file_upload:hover{
                            background: #F3F7FF;
                            border-color: #03B187;
                            .df{
                                display:block;
                            }
                        }
                    }
                }
            }
       }
       .end_remarks{
            margin-top:20px;
            min-height:40px;
            line-height:20px;
            color:#aaa;
            font-size:14px;  
            text-indent:30px;   
        }
       .t_footer{
          height: 40px;
          width: 734px;
          margin: 30px 0;
          .el-button{
              padding: 10px 40px;
          }
       }
    }
}
</style>



